<script>
import Action from "./Action.svelte"
import Passive from "./Passive.svelte"

export let doc
export let open = false

function toggle() {
  open = !open
}
console.log(doc)
</script>

<article style="{open ? '' : 'columns: 1;'}">
  <h2 on:click={toggle}>{doc.name}</h2>
  <p><i>{doc.blurb}</i></p>
  <p style="padding-bottom: 1rem;"><b>{doc.class} {doc.level}</b></p>
  {#if open}
    <table class="statblock">
      <caption>Stats</caption>
      <tbody>
        {#each Object.entries(doc.stats) as [stat, value]}
          <tr><td class="stat"><b>{stat}</b></td><td>{value}<td></tr>
        {/each}
      </tbody>
    </table>

    {#each doc.passives as passive}
      <Passive bind:doc={passive}/>
    {/each}

    {#each doc.actions as action}
      <Action bind:doc={action}/>
    {/each}
  {/if}
</article>

