<script>
export let doc
export let open = true
let color
if (doc.type === "Arcana") {
  color = "var(--arcana-color)"
} else if (doc.type === "Discipline") {
  color = "var(--discipline-color)"
} else {
  color = "var(--default-background-color)"
}

function toggle() {
  open = !open
  console.log("hello", open)
}
</script>

<section>
  <h5 on:click={toggle} class="openable" style="background-color: {color}">{doc.name}</h5>
  {#if open}
    <p><b>{doc.action}{#if doc.repeatable}, repeatable{/if}</b>{#if doc.ritual}&nbsp{doc.ritual}{/if}</p>
    {#if doc.uses}
      <p><b>Uses</b> {doc.uses}</p>
    {/if}
    {#if doc.concentration}
      <p><b>Concentration</b> {doc.concentration}</p>
    {/if}
    {#if doc.duration}
      <p><b>Duration</b> {doc.duration}</p>
    {/if}
    <p><b>Description</b> {doc.description}
  {/if}
</section>
