function system (archetypes, aspects, classes, powers) {
  console.log(powers)
  const sys = {
    aspects,
    powers,
    archetypes: {},
    classes: {},
    emptySelections: () => ({
      name: "",
      blurb: "",
      level: 1,
      class: null,
      initAffinities: {
        Air: 1,
        Earth: 1,
        Fire: 1,
        Water: 1,
      },
      affinities: [],
      aspects: [],
      powers: [],
    })
  }
  sys.apply = (selections) => {
    const character = {
      name: selections.name,
      blurb: selections.blurb,
      level: selections.level,
      class: sys.classes[selections.class].name,
      stats: {
        Evasion: 0,
        Defense: 0,
        Resilience: 0,
        Speed: 0,
        Energy: 0,
        Focus: 0
      },
      passives: [],
      actions: []
    }
    const affinities = {
      Air: selections.initAffinities.Air,
      Earth: selections.initAffinities.Earth,
      Fire: selections.initAffinities.Fire,
      Water: selections.initAffinities.Water
    }
    selections.affinities.forEach(aff => {affinities[aff] += 1})
    character.stats.Evasion = affinities.Air + affinities.Water
    character.stats.Defense = affinities.Earth + affinities.Water
    character.stats.Resilience = affinities.Earth + affinities.Fire
    character.stats.Speed = affinities.Air + affinities.Earth
    character.stats.Energy = affinities.Air + affinities.Fire
    character.stats.Focus = affinities.Fire + affinities.Water

    selections.aspects.forEach(asp => {character.passives.push(...sys.aspects[asp].passives)})
    selections.powers.forEach(pow => {character.actions.push(...sys.powers[pow].actions)})

    return character
  }
  console.log(archetypes)
  console.log(Object.keys(archetypes))
  console.log(archetypes[Object.keys(archetypes)[0]])
  Object.keys(archetypes).forEach(arch => {
    sys.archetypes[arch] = {
      name: archetypes[arch].name,
      aspects: archetypes[arch].aspects.map(asp => sys.aspects[asp]),
      powers: archetypes[arch].powers.map(pow => sys.powers[pow])
    }
  }, {})
  Object.keys(classes).forEach(cls => {
    sys.classes[cls] = {
      name: classes[cls].name,
      archetypes: classes[cls].archetypes.map(arch => sys.archetypes[arch]),
    }
  }, {})

  return sys
}

export default system
