<script>
import Action from "./Action.svelte"
export let doc
export let open = true
console.log(doc)

let color = doc.type === "base" ? "var(--class-aspect-color)" : doc.type === "advanced" ? "var(--class-aspect-color)" : "var(--archetype-aspect-color)"
</script>

<section>
  <h5 on:click={() => open = !open} class="openable" style="background-color: {color}">{doc.name}</h5>
  <p><i>{doc.type}</i></p>
  {#if open}
    {#each doc.actions ? doc.actions : [] as action}
      <Action bind:doc={action}/>
    {/each}
  {/if}
</section>
